.messageEnter {
    width: 91%;
    height: 2.25rem;
    margin: 0 auto;
    padding-top: 0.45rem;
    box-sizing: border-box;
}

.messageEnter .portrait {
    width: 1.35rem;
    height: 1.35rem;
    /*background: url(../../images/touxiang-grey.png) no-repeat center center;*/
    background-size: contain;
    float: left;
}

.messageEnter .bell {
    margin-right: 6px;
    margin-top: 0.26rem;
    width: 0.84rem;
    height: 0.93rem;
    /*background: url(../../images/bell-icon.png) no-repeat center center;*/
    background-size: contain;
    float: right;
    position: relative;
}

.messageEnter .bell span {
    line-height: 16px;
    position: absolute;
    top: -3px;
    right: -11px;
    width: 16px;
    height: 16px;
    display: block;
    background: #ff8a10;
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    border-radius: 50%;
    font-size: 0.55rem;
    color: #fff;
    text-align: center;
}

.messageEnter .bell span i {
	width: 16px;
    display: block;
    transform: scale(0.8);
    -webkit-transform: scale(0.8);
    -moz-transform: scale(0.8);
    transform-origin: center;
    -moz-transform-origin: center;
    -webkit-transform-origin: center;
}

.menu {
    width: 100%;
    height: 3.2rem;
    border-bottom: 1px solid #ddd;
    margin-bottom: 0.3rem;
}

.menu ul {
    width: 96%;
    margin: 0 auto;
    padding-top: 0.53rem;
    box-sizing: border-box;
}

.menu li {
    width: 25%;
    height: 100%;
    float: left;
    text-align: center;
}

.menu li dt {
    width: 1.35rem;
    height: 1.35rem;
    margin: 0 auto;
}

.menu .kownmore dt {
    /*background: url(../../images/knowmore-icon.png) no-repeat center center;*/
    background-size: contain;
}

.menu .strategy dt {
    /*background: url(../../images/strategy-icon.png) no-repeat center center;*/
    background-size: contain;
}

.menu .community dt {
    /*background: url(../../images/community-icon.png) no-repeat center center;*/
    background-size: contain;
}

.menu .invite dt {
    /*background: url(../../images/invite-icon.png) no-repeat center center;*/
    background-size: contain;
}

.menu li dd {
    font-size: 0.36rem;
    color: #444;
    line-height: 1.1rem;
} 

.portio {
	width: 4.8rem;
    float: right;
}

.portio  p{
	padding-top: 0.8rem;
    text-align: left;
    line-height: 1rem;
    font-size: 0.625rem;
    color: #999;
	text-align: right;
}

.portio .p1 {
	padding-top: 0.375rem;
    line-height: 1.5rem;
    font-size: 1.2rem;
    color: #333;
    text-align: right;
}

.portio em {
	font-size: 0.625rem;
    color: #777777;
} 


